.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}
.labels {
  font-size: 14;
  font-weight: bold;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

#button {
  padding: 4px;
  position: absolute;

  right: 10px;
  top: 10px;
  z-index: 1;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Lists {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
}
.List {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: auto;
}
.Card {
  display: flex;
  padding: 0px;
  border-radius: 8px;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;

  border-style: solid;
  background: #93a8e2;
  border-color: aliceblue;
}
.CardTitle {
  flex-wrap: nowrap;
  color: #0c0d27;
  font-weight: 700;
  width: 100%;
}

.Outline {
  border-style: solid;
}

.regular-checkbox {
  -webkit-appearance: none;
  background-color: #fafafa;
  border: 1px solid #cacece;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05),
    inset 0px -15px 10px -12px rgba(0, 0, 0, 0.05);
  padding: 9px;
  border-radius: 3px;
  display: inline-block;
  position: relative;
}

.Check {
  border-style: solid;
  background-color: #268a47;
  color: #ffffff;
  padding: 2px 15px 2px 15px;
  margin: 10px;
  display: flex;
  justify-content: center;
  height: min-content;
  width: auto;
  border-radius: 8px;
}
.Check::after {
  color: #ffffff;
  padding: 2px 15px 2px 15px;
  margin: 10px;
  display: flex;
  justify-content: center;
  width: auto;
  content: "Add to list";
}

.Check:checked {
  border-style: solid;
  background-color: #000000;
  padding: 2px 15px 2px 15px;
  color: #ffffff;
  margin: 10px;
  display: flex;
  justify-content: center;
  width: auto;
  content: "Remove";
}
.Check:hover,
Check:checked:hover {
  border-style: solid;
  background-color: #b18c8c;
  color: #ffffff;
  padding: 2px 15px 2px 15px;
  margin: 10px;
  display: flex;
  justify-content: center;
  width: auto;
}

.Check:active,
Check:checked:active {
  border-style: solid;
  background-color: #6e1414;
  color: #ffffff;
  padding: 2px 15px 2px 15px;
  margin: 10px;
  display: flex;
  justify-content: center;
  width: auto;
}
.Check:active::after {
  color: #ffffff;
  content: "Add";
  display: flex;
  justify-content: center;
  width: auto;
}

.Check:checked::after {
  color: #ffffff;
  padding: 2px 15px 2px 15px;
  margin: 10px;
  display: flex;
  justify-content: center;
  width: auto;
  content: "Remove";
}
.SubCard {
  width: auto;
  background: #264086;
  margin: 10px;
  padding: 4px 30px 20px 30px;
  border-radius: 8px;
  align-items: center;
  justify-content: flex-start;
  border-style: solid;
  border-color: aliceblue;
  display: inline-block;
}

.CardButton {
  display: inline-block;
  border-style: solid;
  align-self: flex-end;
  justify-self: center;
  border-color: aliceblue;
  width: 100%;
  border-radius: 5px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
